import React from "react";
import { Link } from "react-router-dom";
import { Breadcrumb, BreadcrumbItem } from "reactstrap";
import { FaHome } from "react-icons/fa";

import { RoutesString } from "pages/routesString";

export default function Breadcrumbs({ items, currentItem }) {
  return (
    <Breadcrumb>
      <BreadcrumbItem active={!currentItem}>
        <Link to={RoutesString.WELCOME}>
          <FaHome />
        </Link>
      </BreadcrumbItem>
      {items &&
        items.map(({ href, title }) => {
          return (
            <BreadcrumbItem>
              <Link to={href}>{title}</Link>
            </BreadcrumbItem>
          );
        })}
      {currentItem && <BreadcrumbItem active>{currentItem}</BreadcrumbItem>}
    </Breadcrumb>
  );
}
