import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { Formik, withFormik } from "formik";
import * as Yup from "yup";
import { useHistory, useLocation } from "react-router-dom";
import "./TrainingMajorEdit.css";

import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import PageHeader from "./components/PageHeader/PageHeader";
import trainingMajorApi from "api/common/trainingMajor";
import trainingProgramApi from "api/common/trainingProgram";
import {
    FaExchangeAlt,
    FaPlus,
    FaSync,
    FaTextWidth,
    FaTrash,
    FaUserAlt,
    FaUsersCog,
} from "react-icons/fa";
import {
    Button,
    Form,
    FormGroup,
    FormText,
    Input,
    InputGroup,
    InputGroupAddon,
    InputGroupText,
    Label,
} from "reactstrap";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { RoutesString } from "pages/routesString";
import { toast } from "react-toastify";
import { isErrors } from "utils/utils";

const initValues = {
    maNganhDaoTao: "",
    ten: "",
    chuongTrinhDaoTao: 0,
};

const TrainingMajorEdit = function ({
    values,
    handleChange,
    resetForm,
    errors,
}) {
    const { t } = useTranslation();

    const history = useHistory();

    const [id, setId] = useState(null);
    const [isUpdatePage, setIsUpdatePage] = useState(false);
    const [notification, setNotification] = useState({
        color: "danger",
        isOpen: false,
        messages: [],
    });
    const [isLoading, setIsLoading] = useState(false);
    const [listTrainingProgram, setlistTrainingProgram] = useState([
        { id: "", ten: "", maCTDT: "" },
    ]);

    const availableStatus = [
        {
            value: true,
            name: t("admin.common.DELETED"),
        },
        {
            value: false,
            name: t("admin.common.AVAILABLE"),
        },
    ];

    useEffect(async () => {
        const parsed = queryString.parse(window.location.search);
        if (parsed.id) {
            setId(parsed.id);
            setIsUpdatePage(true);
            updateForm(parsed.id);
        } else {
            const res2 = await trainingProgramApi.getAll();
            if (res2) {
                setlistTrainingProgram(res2.contents);
            }
        }
    }, []);

    const updateForm = async function (id) {
        setIsLoading(true);
        try {
            const res = await trainingMajorApi.getById(id);
            if (res) {
                values.maNganhDaoTao = res.maNganhDaoTao;
                values.ten = res.ten;
                values.chuongTrinhDaoTao = res.chuongTrinhDaoTao.id;
            }
            const res2 = await trainingProgramApi.getAll();
            if (res2) {
                setlistTrainingProgram(res2.contents);
            } // eslint-disable-next-line
        } catch (err) {
            if (err.response) {
                if (err?.response?.status === 403 || err?.response?.status === 401) {
                    toast.error(t("RESPONSE_MESSAGE401"));
                } else if (err?.response?.status === 400) {
                    toast.error(t("RESPONSE_MESSAGE400"));
                } else if (err?.response?.status === 409) {
                    toast.error(t("RESPONSE_MESSAGE409"));
                } else if (err?.response?.status === 404) {
                    toast.error(t("RESPONSE_MESSAGE404"));
                } else {
                    toast.error(t("RESPONSE_MESSAGE500"));
                }
            }
        }
        setIsLoading(false);
    };

    const resetNoti = () => {
        setNotification({
            color: "success",
            isOpen: false,
            messages: [],
        });
    };

    const handleEditOnClick = async function (e) {
        e.preventDefault();
        resetNoti();
        try {
            let valuesSend = {};
            valuesSend.maNganhDaoTao = values.maNganhDaoTao;
            valuesSend.ten = values.ten;
            valuesSend.chuongTrinhDaoTao = parseInt(
                values.chuongTrinhDaoTao,
                10
            );
            if (isUpdatePage) {
                const res = await trainingMajorApi.putById(id, valuesSend);
                if (res) {
                    toast.success(t("admin.UPDATE_SUCCESS"));
                    updateForm(id);
                }
            } else {
                for (const key in valuesSend) {
                    if (Object.hasOwnProperty.call(valuesSend, key)) {
                        const element = valuesSend[key];
                        if (element === "" || element === null) {
                            setNotification({
                                color: "danger",
                                isOpen: true,
                                messages: [t("nganhdaotao.DATA_MISSING")],
                            });
                            return;
                        }
                    }
                }
                const res = await trainingMajorApi.post(valuesSend);
                if (res) {
                    setNotification({
                        color: "success",
                        isOpen: true,
                        messages: [t("admin.ADD_SUCCESS")],
                    });
                }
            }
        } catch (err) {
            if (err?.response) {
                if (err?.response?.status === 403 || err?.response?.status === 401) {
                    toast.error(t("RESPONSE_MESSAGE401"));
                } else if (err?.response?.status === 400) {
                    toast.error(t("RESPONSE_MESSAGE400"));
                } else if (err?.response?.status === 409) {
                    toast.error(t("RESPONSE_MESSAGE409"));
                } else if (err?.response?.status === 404) {
                    toast.error(t("RESPONSE_MESSAGE404"));
                } else {
                    if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
                        toast.error(t("ER_DUP_ENTRY"));
                    } else if (String(err?.response?.data?.error)?.includes("NGANHDAOTAO_NAME_EXIST")) {
                        toast.error(t("nganhdaotao.NGANHDAOTAO_NAME_EXIST"));
                    } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
                        toast.error(t("RESPONSE_MESSAGE404"));
                    } else {
                        toast.error(t("RESPONSE_MESSAGE500"));
                    }
                }
            }
        }
    };

    const handleRefreshOnClick = function (e) {
        e.preventDefault();
        setNotification({
            color: "success",
            isOpen: false,
            messages: [],
        });
        resetForm();
    };

    const handleDeleteOnClick = async function (e) {
        e.preventDefault();
        resetNoti();
        try {
            const res = await trainingMajorApi.deleteById(id);
            if (res) {
                if (res.message === "DELETE_NGANHDAOTAO_SUCCESSFULLY") {
                    toast.success(t("admin.common.DELETED"));
                    history.push(RoutesString.ADMIN_TRAINING_MAJOR_LIST);
                } else {
                    setNotification({
                        color: "danger",
                        isOpen: true,
                        messages: [t("ERROR")],
                    });
                }
            } // eslint-disable-next-line
        } catch (err) {
            if (err.response) {
                if (err?.response?.status === 403 || err?.response?.status === 401) {
                    toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE401"));
                } else if (err?.response?.status === 400) {
                    toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
                } else if (err?.response?.status === 409) {
                    toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
                } else if (err?.response?.status === 404) {
                    toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
                } else {
                    if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
                        toast.error(t("RESPONSE_MESSAGE404"));
                    } else {
                        toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
                    }
                }
            }
        }
    };

    return (
        <div className="page">
            {isLoading && <LoadingBar />}

            <Breadcrumbs
                currentItem={
                    isUpdatePage
                        ? t("admin.training_major_edit.UPDATE")
                        : t("admin.training_major_edit.ADD")
                }
                items={[
                    {
                        href: RoutesString.ADMIN_TRAINING_MAJOR_LIST,
                        title: t("admin.training_major_list.LIST"),
                    },
                ]}
            />

            <PageHeader
                headerTitle={
                    isUpdatePage
                        ? t("admin.training_major_edit.UPDATE")
                        : t("admin.training_major_edit.ADD")
                }
            />

            <div className="page-content">
                <Form>
                    <Notification
                        color={notification.color}
                        isOpen={notification.isOpen}
                        messages={notification.messages}
                    />
                    <FormGroup className="row">
                        <Label for="value" className="col-lg-3">
                            {t("admin.training_major_edit.CODE")}{" "}
                            <AsteriskIcon />
                        </Label>
                        <div className="col-lg-6">
                            <InputGroup>
                                <InputGroupAddon addonType="prepend">
                                    <InputGroupText>
                                        <FaUserAlt />
                                    </InputGroupText>
                                </InputGroupAddon>
                                <Input
                                    type="text"
                                    name="maNganhDaoTao"
                                    placeholder={t(
                                        "admin.training_major_edit.CODE_PLACEHOLDER"
                                    )}
                                    value={values.maNganhDaoTao}
                                    onChange={handleChange}
                                    invalid={errors.maNganhDaoTao}
                                    readOnly={isUpdatePage}
                                    required
                                />
                            </InputGroup>
                            {errors.maNganhDaoTao && (
                                <FormText color="danger">
                                    {errors.maNganhDaoTao}
                                </FormText>
                            )}
                        </div>
                    </FormGroup>{" "}
                    <br />
                    <FormGroup className="row">
                        <Label for="name" className="col-lg-3">
                            {t("admin.training_major_list.NAME")}{" "}
                            <AsteriskIcon />
                        </Label>
                        <div className="col-lg-6">
                            <InputGroup>
                                <InputGroupAddon addonType="prepend">
                                    <InputGroupText>
                                        <FaTextWidth />
                                    </InputGroupText>
                                </InputGroupAddon>
                                <Input
                                    type="text"
                                    name="ten"
                                    placeholder={t(
                                        "admin.training_major_edit.NAME_PLACEHOLDER"
                                    )}
                                    value={values.ten}
                                    onChange={handleChange}
                                    invalid={errors.ten}
                                />
                            </InputGroup>
                            {errors.ten && (
                                <FormText color="danger">{errors.ten}</FormText>
                            )}
                        </div>
                    </FormGroup>{" "}
                    <br />
                    <FormGroup className="row">
                        <Label for="name" className="col-lg-3">
                            {t("admin.training_major_list.PROGRAM")}{" "}
                            <AsteriskIcon />
                        </Label>
                        <div className="col-lg-6">
                            <InputGroup>
                                <InputGroupAddon addonType="prepend">
                                    <InputGroupText>
                                        <FaTextWidth />
                                    </InputGroupText>
                                </InputGroupAddon>
                                <Input
                                    type="select"
                                    name="chuongTrinhDaoTao"
                                    value={values.chuongTrinhDaoTao}
                                    onChange={handleChange}
                                >
                                    <option key={1} value={0}>
                                        {t(
                                            "admin.training_major_edit.PROGRAM_PLACEHOLDER"
                                        )}
                                    </option>
                                    {listTrainingProgram.length > 0 &&
                                        listTrainingProgram.map(
                                            (item, index) => {
                                                return (
                                                    <option
                                                        key={index + 1}
                                                        value={item.id}
                                                    >
                                                        {t(
                                                            "admin.training_major_list.PROGRAM_CODE"
                                                        )}
                                                        : {item.maCTDT}&ensp;
                                                        {"/"}&ensp;
                                                        {t(
                                                            "admin.training_major_list.PROGRAM_NAME"
                                                        )}
                                                        : {item.ten}
                                                    </option>
                                                );
                                            }
                                        )}
                                </Input>
                            </InputGroup>
                            {errors.chuongTrinhDaoTao && (
                                <FormText color="danger">
                                    {errors.chuongTrinhDaoTao}
                                </FormText>
                            )}
                        </div>
                    </FormGroup>{" "}
                    <br />
                    <FormGroup>
                        <Button color="success" onClick={handleEditOnClick} disabled={isErrors(errors)}>
                            {isUpdatePage ? (
                                <>
                                    <FaExchangeAlt /> {t("admin.common.UPDATE")}
                                </>
                            ) : (
                                <>
                                    <FaPlus /> {t("admin.common.ADD")}
                                </>
                            )}
                        </Button>
                        &emsp;
                        {isUpdatePage && (
                            <>
                                <Button onClick={handleRefreshOnClick}>
                                    <FaSync /> {t("admin.common.REFRESH")}
                                </Button>
                                &emsp;
                                <Button
                                    color="danger"
                                    onClick={handleDeleteOnClick}
                                >
                                    <FaTrash /> {t("admin.common.DELETE")}
                                </Button>
                            </>
                        )}
                    </FormGroup>
                </Form>
            </div>
        </div>
    );
};

const TrainingMajorEditFormikForm = withFormik({
    mapPropsToValues() {
        return {
            maNganhDaoTao: "",
            ten: "",
            chuongTrinhDaoTao: null,
        };
    },
    enableReinitialize: true,
    validationSchema: Yup.object().shape({
        maNganhDaoTao: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
        ten: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
    }),
})(TrainingMajorEdit);

export default TrainingMajorEditFormikForm;
