import axiosClient from "./axiosClient";
import queryString from "query-string";

export default {
  getAll: function (filter = {}) {
    const query = queryString.stringify({limit:-1,page:0, ...filter });
    const url = `chuong-trinh-dao-tao?${query}`;
    return axiosClient.get(url);
  },
  getList: function (pageable) {
    let url = `chuong-trinh-dao-tao?page=${pageable.page - 1}&limit=${
      pageable.limit
    }`;
    if (pageable.sorter.sortBy && pageable.sorter.sortType) {
      url += `&sortBy=${pageable.sorter.sortBy}&sortType=${pageable.sorter.sortType}`;
    }
    if (pageable.searchKey) {
      url += `&search=${pageable.searchKey}`;
    }
    if (pageable.filter.isDeleted !== null) {
      url += `&isDeleted=${pageable.filter.isDeleted}`;
    }
    return axiosClient.get(url);
  },
  getById: function (id) {
    const url = `chuong-trinh-dao-tao/${id}`;
    return axiosClient.get(url);
  },
  post: function (body) {
    const url = `chuong-trinh-dao-tao`;
    return axiosClient.post(url, JSON.stringify(body));
  },
  putById: function (id, body) {
    const url = `chuong-trinh-dao-tao/${id}`;
    return axiosClient.put(url, JSON.stringify(body));
  },
  deleteById: function (id) {
    const url = `chuong-trinh-dao-tao/${id}`;
    return axiosClient.delete(url);
  },
};
